
























import { Component, Vue, Watch } from 'vue-property-decorator';
import FeedItem from '@/components/shared/FeedItem.vue';
import axios from 'axios';

@Component({
  components: {
    FeedItem,
  },
})
export default class Feed extends Vue {

  private feedLoading = false;
  private feedItems = [];

  created() {
    this.getFeed();
  }

  private getFeed() {
    this.feedLoading = true;
    axios.get('https://borrowing-svc-api.appspot.com/utils/feed')
      .then((res) => {
        // If the resource has changed (304 is not modified)
        if (res.status !== 304) {
          this.feedItems = res.data.slice(0, 10);
        }
      }).catch((err) => {

      }).finally(() => {
        this.feedLoading = false;
      });
  }
}

